
'use client';

import { useState, useRef, useEffect } from 'react';
import Image from 'next/image';
import { Camera, Sparkles, Wand2, ShoppingCart, RefreshCcw, Download, ArrowDown, Share2 } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { handleGeneratePortrait } from '@/app/actions';
import type { GenerateActionResult } from '@/app/actions';
import { useIsMobile } from '@/hooks/use-mobile';

import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Skeleton } from '@/components/ui/skeleton';
import { Textarea } from './ui/textarea';
import { cn } from '@/lib/utils';

const allHatOptions = [
  'Top Hat', 'Cowboy Hat', 'Beanie', 'Fez', 'Beret', 'Baseball Cap', 
  'Graduation Cap', 'Sombrero', 'Wizard Hat', 'Chef Hat', 'Sailor Hat', 
  'Detective Hat', 'Propeller Beanie', 'Crown', 'Viking Helmet', 'Pirate Hat',
  'Hard Hat', 'Jester Hat', 'Bowler Hat', 'Fedora', 'Santa Hat', 'Elf Hat',
  'Newsboy Cap', 'Turban', 'Ushanka', 'Safari Helmet', 'Mortar Board',
  'Party Hat', 'Tricorne', 'Pork Pie Hat', 'Kepi', 'Deerstalker'
];

const HATS_TO_SHOW = 15;

type PortraitState = 'idle' | 'generating' | 'generated';

export default function PortraitGeneratorForm() {
  const { toast } = useToast();
  const isMobile = useIsMobile();
  
  // Form input state
  const [photoPreview, setPhotoPreview] = useState<string | null>(null);
  const [photoDataUri, setPhotoDataUri] = useState<string>('');
  const [petName, setPetName] = useState('');
  const [selectedHat, setSelectedHat] = useState('');
  const [customHat, setCustomHat] = useState('');
  
  // Generation and UI flow state
  const [portraitState, setPortraitState] = useState<PortraitState>('idle');
  const [generatedPortraitUri, setGeneratedPortraitUri] = useState<string>('');
  const [displayedHats, setDisplayedHats] = useState<string[]>([]);
  
  const fileInputRef = useRef<HTMLInputElement>(null);
  const portraitSectionRef = useRef<HTMLDivElement>(null);
  
  const getHatStyle = () => customHat.trim() || selectedHat;
  
  const createNew = () => {
    setPortraitState('idle');
    setGeneratedPortraitUri('');
    setSelectedHat('');
    setCustomHat('');
  };
  
  const shuffleHats = () => {
    const shuffled = [...allHatOptions].sort(() => 0.5 - Math.random());
    setDisplayedHats(shuffled.slice(0, HATS_TO_SHOW));
  };
  
  useEffect(() => {
    shuffleHats();
  }, []);

  useEffect(() => {
    if (portraitState === 'generated') {
      portraitSectionRef.current?.scrollIntoView({ behavior: 'smooth', block: 'center' });
    }
  }, [portraitState]);

  const handleFileChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const file = event.target.files?.[0];
    if (file) {
      setPhotoPreview(URL.createObjectURL(file));

      const reader = new FileReader();
      reader.onloadend = () => {
        const dataUri = reader.result as string;
        setPhotoDataUri(dataUri);
      };
      reader.readAsDataURL(file);
    }
  };
  
  const handleGenerationSubmit = async (event: React.FormEvent<HTMLFormElement>) => {
    event.preventDefault();
    
    const hatStyle = getHatStyle();

    if (!photoDataUri) {
      toast({ title: 'Oops!', description: 'Please upload a photo of your pet.', variant: 'destructive' });
      return;
    }
    if (!petName) {
      toast({ title: 'Oops!', description: "Please enter your pet's name.", variant: 'destructive' });
      return;
    }
    if (!hatStyle) {
      toast({ title: 'Oops!', description: 'Please select or describe a hat style.', variant: 'destructive' });
      return;
    }
    
    setPortraitState('generating');
    setGeneratedPortraitUri('');

    const result: GenerateActionResult = await handleGeneratePortrait({ petName, photoDataUri, hatStyle });
    
    if (result.success && result.portraitDataUri) {
      setGeneratedPortraitUri(result.portraitDataUri);
      setPortraitState('generated');
    } else {
      toast({ 
        title: 'Generation Failed', 
        description: result.message || 'An unexpected error occurred. Please try again.', 
        variant: 'destructive',
        duration: 9000,
      });
      setPortraitState('idle');
    }
  }

  const handleHatSelect = (hat: string) => {
    setSelectedHat(hat);
    setCustomHat('');
  }
  
  const handleCustomHatChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
      setCustomHat(e.target.value);
      setSelectedHat('');
  }

  const applyWatermarkAndConvertToJpeg = (base64Png: string): Promise<string> => {
    return new Promise((resolve, reject) => {
      const image = new window.Image();
      image.onload = () => {
        const canvas = document.createElement('canvas');
        canvas.width = image.width;
        canvas.height = image.height;
        const ctx = canvas.getContext('2d');
        if (!ctx) {
          return reject(new Error('Could not get canvas context'));
        }

        // Draw the main image
        ctx.drawImage(image, 0, 0);

        // Prepare watermark text
        const watermarkText = 'LicksAndLids.com';
        const fontSize = Math.max(18, Math.min(canvas.width * 0.04, 32));
        ctx.font = `bold ${fontSize}px "Poppins", sans-serif`;
        ctx.fillStyle = 'rgba(255, 255, 255, 0.7)';
        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';

        // Add a subtle shadow/outline for better visibility
        ctx.shadowColor = 'rgba(0, 0, 0, 0.8)';
        ctx.shadowBlur = 5;
        
        // Draw watermark in the bottom center
        ctx.fillText(watermarkText, canvas.width / 2, canvas.height - (fontSize * 1.5));

        // Convert to JPEG
        const jpegDataUri = canvas.toDataURL('image/jpeg', 0.9);
        resolve(jpegDataUri);
      };
      image.onerror = (err) => {
        reject(err);
      };
      image.src = base64Png;
    });
  };

  const handleDownload = async () => {
    if (!generatedPortraitUri) return;

    try {
        const watermarkedJpegUri = await applyWatermarkAndConvertToJpeg(generatedPortraitUri);

        if (isMobile) {
            window.open(watermarkedJpegUri, '_blank');
            toast({
                title: 'Image opened in new tab',
                description: 'Long-press the image to save it to your photos.',
            });
            return;
        }

        const link = document.createElement('a');
        link.href = watermarkedJpegUri;
        const petNameForFile = (petName || 'pet').toLowerCase().replace(/\s+/g, '-');
        const hatStyleForFile = (getHatStyle() || 'portrait').toLowerCase().replace(/\s+/g, '-');
        link.download = `${petNameForFile}-${hatStyleForFile}.jpeg`;

        document.body.appendChild(link);
        link.click();
        document.body.removeChild(link);
    } catch (error) {
        console.error('Download failed:', error);
        toast({
            title: 'Download Failed',
            description: 'Could not prepare image for download. Please try again.',
            variant: 'destructive',
        });
        // Fallback for older browsers or errors: open original PNG
        window.open(generatedPortraitUri, '_blank');
    }
  };

  const handleShare = async () => {
    if (!generatedPortraitUri) return;
  
    const hatStyle = getHatStyle();
    const shareText = `Check out this portrait of ${petName} wearing a ${hatStyle}! Created at Licks & Lids. #LicksAndLids`;
    const shareTitle = `Licks & Lids: ${petName}'s Portrait!`;
    const petNameForFile = (petName || 'pet').toLowerCase().replace(/\s+/g, '-');
    const hatStyleForFile = (getHatStyle() || 'portrait').toLowerCase().replace(/\s+/g, '-');
    const filename = `${petNameForFile}-${hatStyleForFile}.jpeg`;
  
    try {
      const watermarkedJpegUri = await applyWatermarkAndConvertToJpeg(generatedPortraitUri);
      const response = await fetch(watermarkedJpegUri);
      const blob = await response.blob();
      const file = new File([blob], filename, { type: 'image/jpeg' });

      const shareData: ShareData = {
        files: [file],
        title: shareTitle,
        text: shareText,
        url: "https://www.licksandlids.com",
      };
  
      if (navigator.canShare && navigator.canShare({ files: [file] })) {
        await navigator.share(shareData);
      } else if (navigator.canShare) {
        // Fallback for browsers that can share but not files (e.g., only text/url)
        await navigator.share({
          title: shareTitle,
          text: shareText,
          url: shareData.url,
        });
      } else {
        throw new Error('Web Share API not supported.');
      }
    } catch (error) {
      if ((error as Error).name !== 'AbortError') {
        console.error('Sharing failed', error);
        toast({
          title: 'Sharing Failed',
          description: 'To share this image, please download it first using the "Download" button, then share it from your photo gallery.',
          variant: 'destructive',
          duration: 9000,
        });
      }
    }
  };

  const isGenerating = portraitState === 'generating';

  return (
    <div className="space-y-8">
      <Card>
        <CardHeader>
          <CardTitle as="h2" className="text-2xl sm:text-3xl">Create Your Masterpiece</CardTitle>
          <CardDescription as="p" className="text-sm sm:text-base">Follow these simple steps to get a portrait of your furry friend.</CardDescription>
        </CardHeader>
        <CardContent>
          <form onSubmit={handleGenerationSubmit} className="space-y-6">
            <div className="space-y-2">
              <Label htmlFor="pet-photo" className="text-base">1. Upload a Photo</Label>
              <div
                role="button"
                aria-label="Upload a photo of your pet"
                tabIndex={0}
                className="relative flex justify-center items-center w-full h-48 md:h-64 border-2 border-dashed rounded-lg cursor-pointer hover:border-primary transition-colors"
                onClick={() => fileInputRef.current?.click()}
                onKeyDown={(e) => { if (e.key === 'Enter' || e.key === ' ') fileInputRef.current?.click() }}
                data-ai-hint="pet photo"
              >
                {photoPreview ? (
                  <>
                  <Image
                    src={photoPreview}
                    alt="Pet preview"
                    fill
                    className={cn("object-contain rounded-lg p-1", isGenerating && "opacity-50")}
                  />
                  </>
                ) : (
                  <div className="text-center text-muted-foreground">
                    <Camera className="mx-auto h-12 w-12" />
                    <p>Click to upload a photo</p>
                  </div>
                )}
              </div>
              <Input
                id="pet-photo"
                name="pet-photo-file"
                type="file"
                accept="image/*"
                ref={fileInputRef}
                className="hidden"
                onChange={handleFileChange}
                disabled={isGenerating}
              />
            </div>

            <div className="space-y-2">
              <Label htmlFor="petName" className="text-base">2. Name Your Pet</Label>
              <Input
                id="petName"
                name="petName"
                placeholder="e.g., Buddy"
                value={petName}
                onChange={(e) => setPetName(e.target.value)}
                disabled={isGenerating}
                className="text-base"
              />
            </div>

           <div className="space-y-4">
              <Label className="text-base">3. Choose a Hat Style</Label>
              <div className="flex flex-wrap gap-2">
                {displayedHats.map((hat) => (
                  <Button
                    key={hat}
                    type="button"
                    variant={selectedHat === hat ? 'default' : 'outline'}
                    onClick={() => handleHatSelect(hat)}
                    disabled={isGenerating}
                  >
                    {hat}
                  </Button>
                ))}
                <Button
                  type="button"
                  variant="secondary"
                  onClick={shuffleHats}
                  disabled={isGenerating}
                >
                  <RefreshCcw className="mr-2 h-4 w-4" />
                  Shuffle Hats
                </Button>
              </div>
               <Textarea
                  name="customHatStyle"
                  placeholder="...or describe a custom hat in detail (e.g., 'a funky rainbow propeller hat')"
                  value={customHat}
                  onChange={handleCustomHatChange}
                  className="mt-2 text-base"
                  disabled={isGenerating}
                />
            </div>

            <Button type="submit" variant="accent" size="lg" className="w-full text-base md:text-lg" disabled={isGenerating}>
              {isGenerating ? (
                <>
                  <Sparkles className="mr-2 h-4 w-4 animate-spin" />
                  Creating Magic...
                </>
              ) : (
                <>
                  <Wand2 className="mr-2 h-4 w-4" />
                  Generate Portrait
                </>
              )}
            </Button>
          </form>
        </CardContent>
      </Card>
      
      {(portraitState === 'generating' || portraitState === 'generated') && (
        <div ref={portraitSectionRef}>
          <div className="flex justify-center items-center text-muted-foreground my-4">
            <ArrowDown className="h-6 w-6 animate-bounce" />
          </div>
          <Card className={cn(portraitState === 'generated' && 'animate-in fade-in zoom-in-95 duration-500')}>
            <CardHeader>
              <CardTitle as="h2" className="text-2xl sm:text-3xl">Your Portrait</CardTitle>
              <CardDescription as="p" className="text-sm sm:text-base">Behold! Your pet's masterpiece.</CardDescription>
            </CardHeader>
            <CardContent>
              <div className="aspect-square w-full rounded-lg bg-muted flex items-center justify-center overflow-hidden" data-ai-hint="generated portrait">
                {isGenerating ? (
                  <div className="w-full h-full flex flex-col items-center justify-center space-y-4 bg-muted p-4">
                    <Skeleton className="h-full w-full" />
                    <p className="text-muted-foreground animate-pulse">Dipping the brushes...</p>
                  </div>
                ) : (generatedPortraitUri &&
                  <Image
                    src={generatedPortraitUri}
                    alt={`Generated portrait of ${petName} wearing a ${getHatStyle()}`}
                    width={1024}
                    height={1024}
                    className="object-cover w-full h-full transition-opacity duration-500 opacity-100"
                    priority
                  />
                )}
              </div>
            </CardContent>
             {portraitState === 'generated' && (
              <CardFooter className="flex-col gap-4">
                  <div className="w-full grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-2">
                      <Button asChild variant="outline" className="w-full text-base sm:text-sm">
                          <a
                           href={`https://www.amazon.com/s?k=${encodeURIComponent(getHatStyle() + ' for pet')}&tag=logonitro-20`}
                           target="_blank"
                           rel="noopener noreferrer"
                          >
                              <ShoppingCart /> 
                              Shop Look
                          </a>
                      </Button>
                      <Button variant="outline" className="w-full text-base sm:text-sm" onClick={createNew}>
                          <RefreshCcw /> 
                          Create New
                      </Button>
                      <Button variant="outline" className="w-full text-base sm:text-sm" onClick={handleDownload}>
                          <Download /> 
                           Download
                      </Button>
                      {typeof navigator !== 'undefined' && navigator.share && (
                        <Button variant="outline" className="w-full text-base sm:text-sm" onClick={handleShare}>
                            <Share2 /> 
                            Share
                        </Button>
                      )}
                  </div>
                  <p className="text-xs text-muted-foreground text-center w-full pt-2">
                      As an Amazon Associate, we earn from qualifying purchases.
                  </p>
              </CardFooter>
             )}
          </Card>
        </div>
      )}
    </div>
  );
}

    